<template>
  <div class="film-detail">
    <h1>{{title}}</h1> 
    <!-- 简介 -->
    <div class="summary clearfix">
      <div class="summary-left">
        <five-star :star="filmDetail[0].rating.stars"></five-star>
        {{filmDetail[0].rating.average}}
        <span class="summary-ratings-count">{{filmDetail[0].ratings_count}}人评价</span>
        <p class="summary-genres">
          {{filmDetail[0].year}} /
          <span v-for="item in filmDetail[0].genres">{{item}} / </span>
          <span v-for="item in filmDetail[0].directors">{{item.name}}(导演) / </span>
          <span v-for="item in filmDetail[0].casts">{{item.name}} / </span>{{filmDetail[0].countries[0]}}
        </p>
      </div>
      <div class="summary-right"><img :src="getImage(filmDetail[0].images.medium)" alt=""/></div>
    </div>
    <div class="btn clearfix">
      <button class="pull-left">想看</button>
      <button class="pull-right">看过</button>
    </div> 
    <!-- 简介详细  -->
    <div class="summary-detail">
      <h2>{{title}}的剧情简介</h2>
      <p>{{filmDetail[0].summary}}</p>
    </div>
    <!-- 电影相关影人 -->
    <div class="role">
      <h2>影人</h2>
      <swiper :options="swiperOption">
        <swiper-slide class="content-box" v-for="item1 in filmDetail[0].directors" :key="item1.id">
          <div class="img-box">
            <img :src="getImage(item1.avatars.small)" :alt="item1.alt"/>
          </div>
          <h2>{{item1.name}}</h2>
          <span>导演</span>
        </swiper-slide>
        <swiper-slide class="content-box" v-for="item2 in filmDetail[0].casts" :key="item2.id">
          <div class="img-box">
            <img :src="getImage(item2.avatars.small)" :alt="item2.alt"/>
          </div>
          <h2>{{item2.name}}</h2>
          <span>演员</span>
        </swiper-slide>
      </swiper>
    </div>
    <footer>
      <douban-footer></douban-footer>
    </footer>
  </div>
</template>

<script>
import fivestar from "../components/fivestar"
import doubanFooter from "./footer"
import $ from '../../static/js/jquery-1.11.3.min.js'

export default {
  data: function(){
    return {
      filmDetail: [],
      title: '',
      swiperOption: {
        slidesPerView: 'auto',
        freeMode: true
      }
    }
  },
  created: function(){
    this.viewTop();
  },
  mounted: function(){
    let id = this.$route.params.id;
    this.getAxios('https://api.douban.com/v2/movie/subject/'+id,this.filmDetail);
  },
  methods: {
    getAxios: function(url,obj){
      $.ajax({
          url:url,
          type: "GET",
          cache: false,
          dataType: "jsonp",
          jsonp: "callback",
          success: function(data){
            obj.push(data);
          },
          error: function(jqXHR){
            console.log("发生错误："+jqXHR.status);
          }
      });
      // this.$http.get(url)
      // .then((response) => {
      //   if(response.status == 200){
      //     Vue.set(obj,0,response.data);
      //   }
      // })
      // .catch(function (error) {
      //   console.log(error);
      // });
    },
    getImage: function(url){
      if(url !== undefined){
          return url.replace(/^http\w{0,1}:\/\//ig,'https://images.weserv.nl/?url=');
      }
    }
  },
  components: {
    'five-star': fivestar,
    'douban-footer': doubanFooter
  },
  watch: {
    filmDetail: function(val){
      this.title = val[0].title;
    }
  }
}
</script>

<style scoped>
@import url("../assets/css/swiper-3.4.2.min.css");

.film-detail h1 {
  font-size: 1.5rem;
  margin-bottom: 5px;
}

.summary .summary-left {
  width: 65%;
  font-size: 1.2rem;
  float: left;
}

.summary .summary-left .summary-ratings-count {
  color:#aaa;
}

.summary-genres {
  line-height: 1.5;
  margin-top: 10px;
}

.summary .summary-right {
  width: 30%;
  float: right;
}

.summary .summary-right img {
  width: 100%;
}

.btn {
  margin: 60px 0 30px 0;
}

.btn button {
  width: 49%;
  padding:5px 0;
  background-color: #fff;
  border: 1px solid #EEB422;
  color:#EEB422;
  border-radius: 5px;
  font-size: 1.2rem;
}

.summary-detail h2 {
  font-size: 1.2rem;
  color:#aaa;
  margin-bottom: 15px;
}

.summary-detail p {
  font-size: 1.3rem;
  line-height: 1.6;
}

.role h2 {
  font-size: 1.2rem;
  color:#aaa;
  margin: 15px 0;
}

.content-box {
  width: 7rem;
  text-align: center;
  font-size: 1.2rem;
}

.content-box + .content-box {
  margin-left: 10px;
}

.img-box img {
  width: 100%;
}

.content-box h2 {
  color: #333;
}

.content-box span {
  color: #aaa;
}


</style>



