<template>
  <div class="header clearfix">
    <router-link class="pull-left" to="/" tag="div"><img src="../assets/picture/logo.png"></router-link>
    <div class="pull-right">
      <ul class="clearfix">
        <router-link tag="li" to="/page/film" style="color:#2384e8">电影</router-link>
        <router-link tag="li" to="/page/book" style="color:#9f7860">图书</router-link>
        <router-link tag="li" to="/page/broadcast" style="color:#e4a813">广播</router-link>
        <router-link tag="li" to="/page/group" style="color:#2abeda">小组</router-link>
        <router-link to="/search" tag="li"><img src="../assets/picture/list.png"></router-link>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
}
</script>

<style scoped>
.header {
  position: fixed;
  top:0;
  height: 4rem;
  line-height: 4rem;
  background: #fff;
  width: 100%;
  max-width: 768px;
  min-width: 320px;
  border-bottom: 1px solid #f3f3f3;
  padding:0 1.5rem;
  z-index: 9999;
}

.header .pull-left {
  width: 4.5rem;
}

.header .pull-left img {
  width: 100%;
  vertical-align: middle;
}

.header ul li {
  float: left;
  font-size: 1.5rem;
}

.header li+li {
  margin-left: 1rem;
}

.header .pull-right li:last-child {
  width: 2.5rem;
}

.header .pull-right li:last-child img {
  width: 100%;
  vertical-align: middle;
}
</style>



