<template>
  <!-- 影院热映 -->
  <div class="most">
    <h2>影院热映</h2> 
    <ul
    v-infinite-scroll="loadMore"
    infinite-scroll-disabled="loading"
    infinite-scroll-distance="10" class="clearfix">
      <router-link :to="{name:'filmDetail',params:{id:item.id}}" tag="li" class="image-box pull-left" v-for="item in show" :key="item.id">
        <div class="content-box">
          <div class="img-box"><img :src="getImage(item.images.medium)" :alt="item.alt"/></div>
          <h2 class="nowrap">{{item.title}}</h2>
          <p>
            <five-star :star="item.rating.stars"></five-star>
            <span>{{item.rating.average}}</span>
          </p>
        </div>
      </router-link>
    </ul>
  </div>
</template>

<script>
import fivestar from "../components/fivestar"
import $ from '../../static/js/jquery-1.11.3.min.js'

export default {
  data: function(){
    return {
      loading: false,
      theatres: [],
      show: []
    }
  },
  components: {
    'five-star': fivestar
  },
  created: function(){
    this.viewTop();
  },
  mounted: function(){
    this.getAxios('https://api.douban.com/v2/movie/in_theaters',this.theatres,this.show);
  },
  methods: {
    loadMore: function(){
      let self = this;
      this.loading = true;
      setTimeout(() => {
        let showLength = self.show.length;
        let max = Math.min(showLength+6,self.theatres.length);
        for (let i = self.show.length; i < max; i++) {
          this.show.push(self.theatres[i]);
        }
        this.loading = false;
      }, 200);
    },
    getImage: function(url){
      if(url !== undefined){
          return url.replace(/^http\w{0,1}:\/\//ig,'https://images.weserv.nl/?url=');
      }
    },
    getAxios: function(url,obj,limit){
      $.ajax({
          url:url,
          type: "GET",
          cache: false,
          dataType: "jsonp",
          jsonp: "callback",
          success: function(data){
            for(var i =0;i<data.subjects.length;i++){
              obj.push(data.subjects[i]);
            } 
            for(let i =0;i<3;i++){
              limit.push(data.subjects[i]);
            }
          },
          error: function(jqXHR){
            console.log("发生错误："+jqXHR.status);
          }
      });
      // this.$http.get(url)
      // .then(function (response) {
      //   if(response.status === 200){
      //     for(let i =0;i<response.data.subjects.length;i++){
      //       obj.push(response.data.subjects[i]);
      //     }
      //     for(let i =0;i<2;i++){
      //       limit.push(response.data.subjects[i]);
      //     }
      //   }
      // })
      // .catch(function (error) {
      //   console.log(error);
      // });
    }
  }
}
</script>

<style scoped>
.content-box p {
  text-align: center;
}

.content-box p span{
  font-size: 1.2rem;
  color:#aaa;
}
</style>



