<template>
  <div id="most" :is="currentView"></div>
</template>

<script>
import theatresMore from './theatresMore'
import top250More from './top250More'
import xugouMore from './xugouMore'
import unxugouMore from './unxugouMore'

export default {
  data: function(){
    return {
      currentView: ''
    }
  },
  created: function(){
    this.toggle();
    this.viewTop();
  },
  components: {
    'theatres-more': theatresMore,
    'top250-more': top250More,
    'xugou-more': xugouMore,
    'unxugou-more': unxugouMore
  },
  methods: {
    toggle: function(){
      let address = this.$route.params.address;
      switch(address){
        case 'theatres':
          this.currentView = 'theatres-more';
          break;
        case 'top250':
          this.currentView = 'top250-more';
          break;
        case 'xugou':
          this.currentView = 'xugou-more';
          break;
        case 'unxugou':
          this.currentView = 'unxugou-more';
          break;
      }
    }
  }
}
</script>

<style>
#most>h2 {
  font-size: 2.2rem;
  margin-bottom: 20px;
}

#most .image-box {
  width:32%;
  margin-bottom: 20px;
}

#most .image-box:nth-of-type(3n-1) {
  margin:0 2%;
}

#most .content-box {
  width: 100%;
}

#most .content-box h2 {
  font-size: 1.3rem;
  text-align: center;
}

#most .content-box .img-box img {
  width:100%;
  height: 12rem;
}
</style>



